import {
  Component,
  Directive,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { FormControl } from '@angular/forms';

import { combineLatest, Observable, of, Subject } from 'rxjs';
import { catchError, map, takeUntil } from 'rxjs/operators';

import { BbTemplate } from '@backbase/foundation-ang/core';
import { CurrencyRegistryResponse } from '@backbase/data-ang/limits';
import { NotificationService } from '@backbase/ui-ang/notification';
import { CurrencyService } from '../services/currency.service';
import { LimitTypes } from '../models/limit-types';
import {
  LegalEntitiesHttpService,
  ServiceAgreementHttpService,
} from '@backbase/data-ang/accesscontrol';

@Component({
  selector: 'bb-manage-limits-modal-header',
  templateUrl: 'manage-limits-modal-header.component.html',
})
export class ManageLimitsModalHeaderComponent implements OnInit, OnDestroy {
  /**
   * Applied currency
   */
  @Input() currency?: string;
  /**
   * Currency drop down form control
   */
  @Input() currencyControl: FormControl = new FormControl();
  /**
   * Default header title
   */
  @Input() defaultTitle!: string;
  /**
   * Header title
   */
  @Input() title?: string;
  /**
   * Service agreement id
   */
  @Input() serviceAgreementId?: string;
  /**
   * Legal entity id
   */
  @Input() legalEntityId?: string;
  /**
   * User name
   */
  @Input() subTitleUserName?: string;
  /**
   * Job role name
   */
  @Input() subTitleJRName?: string;
  /**
   * Legal entity name
   */
  @Input() subTitleLEName?: string;
  /**
   * Service agreement name
   */
  @Input() subTitleSAName?: string;
  /**
   * Notification dismiss time in seconds
   */
  @Input() notificationDismissTime!: number;
  /**
   * Close modal window event.
   * Propagate event when modal window should be closed.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close = new EventEmitter<void>();
  /**
   * Currency error notification header template reference
   */
  @ViewChild('currencyErrorNotificationHeader', { read: TemplateRef, static: true })
  currencyErrorNotificationHeader: TemplateRef<any> | undefined;
  /**
   * Currency error notification message template reference
   */
  @ViewChild('currencyErrorNotificationMessage', { read: TemplateRef, static: true })
  currencyErrorNotificationMessage: TemplateRef<any> | undefined;

  /**
   * Limit types enumeration
   */
  readonly LimitTypes = LimitTypes;

  /**
   * Limit types stream
   */
  limitType$$: Observable<LimitTypes | boolean> = of(true);

  /**
   * Service agreement name stream
   */
  saName$$: Observable<string | undefined> = of(undefined);

  /**
   * Legal entity name stream
   */
  leName$$: Observable<string | undefined> = of(undefined);

  /**
   * Show notification stream
   */
  private readonly showNotification$ = new Subject<{
    templateRef: TemplateRef<any> | string;
    modifier: 'success' | 'error' | 'warning';
  }>();
  /**
   * Unsubscribe stream
   */
  private destroy$ = new Subject<void>();

  /**
   * Currency Info stream
   */
  readonly currencyInfo$: Observable<boolean | CurrencyRegistryResponse | undefined> = this.currencyListService
    .currencyInfo()
    .pipe(
      map((currencyInfo: CurrencyRegistryResponse | undefined) => {
        if (!this.currency) {
          // show error message if no default currency
          if (currencyInfo && currencyInfo.defaultCurrency) {
            this.currencyControl.setValue(currencyInfo.defaultCurrency);
          } else {
            this.closeModal();
          }
        }
        return currencyInfo;
      }),
      // show error message if currency is not set already
      catchError(() => {
        if (!this.currency) {
          this.closeModal();
        }
        return of(false);
      }),
    );

  constructor(
    private readonly currencyListService: CurrencyService,
    private readonly notificationService: NotificationService,
    private readonly serviceAgreementHttpService: ServiceAgreementHttpService,
    private readonly legalEntitiesService: LegalEntitiesHttpService,
  ) {}

  ngOnInit(): void {
    this.saName$$ = this.serviceAgreementId ? this.serviceAgreementHttpService
      .getServiceAgreementItem({ serviceAgreementId: this.serviceAgreementId})
      .pipe(
        map(sa => sa.name),
        catchError(() => of(undefined)),
      ) : of(this.subTitleSAName);

    this.leName$$ = this.legalEntityId ? this.legalEntitiesService
      .getLegalEntityById({ legalEntityId: this.legalEntityId})
      .pipe(
        map(le => le.name),
        catchError(() => of(undefined)),
      ) : of(this.subTitleLEName);

    this.limitType$$ = combineLatest([ this.saName$$, this.leName$$ ])
      .pipe(
      map(([saName, leName]: [string | undefined, string | undefined]) => {
        if (leName && !saName && !this.subTitleUserName && !this.subTitleJRName) {
          return LimitTypes.LE_BF_PRIVILEGE;
        } else if (leName && this.subTitleUserName && !saName && !this.subTitleJRName) {
          return LimitTypes.LE_USER;
        } else if (this.subTitleUserName && this.subTitleJRName && !leName && !saName) {
          return LimitTypes.USER_JR_BF_PRIVILEGE;
        } else if (saName && !leName && !this.subTitleUserName && !this.subTitleJRName) {
          return LimitTypes.SA;
        } else if (saName && this.subTitleUserName && !this.subTitleJRName && !leName) {
          return LimitTypes.SA_USER;
        } else if (saName && leName && !this.subTitleJRName && !this.subTitleUserName) {
          return LimitTypes.SA_LE;
        } else if (saName && this.subTitleJRName && !leName && !this.subTitleUserName) {
          return LimitTypes.SA_JR_BF_PRIVILEGE;
        } else {
          return true;
        }
      }),
    );

    this.showNotification$.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.notificationService.showNotification({
        modifier: 'error',
        header: this.currencyErrorNotificationHeader as TemplateRef<any>,
        message: this.currencyErrorNotificationMessage as TemplateRef<any>,
        dismissible: true,
        ttl: this.notificationDismissTime,
      });
    });
  }

  /**
   * Show error notification and close modal
   */
  private closeModal(): void {
    this.showNotification$.next();
    this.close.emit();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}

@Directive({
  selector: '[bbManageLimitsModalHeaderCustomizable]',
})
export class ManageLimitsModalHeaderCustomizableDirective extends BbTemplate<
  ManageLimitsModalHeaderComponent,
  {
    currencyInfo: boolean | CurrencyRegistryResponse | null | undefined;
    saName: string | undefined | null;
    leName: string | undefined | null;
    limitType: string | boolean | null;
  }
> {}
